import React, { useState } from 'react';
import { BigNumber } from 'ethers'
import { getContract } from '../util/interact';

const lbl_claim_title = 'CLAIM YOUR BRAIN!';
const lbl_start_minting = 'MINTING DATE WILL BE ANNOUNCED SOON!';
const View_brain = 'VIEW YOUR BRAIN ON OPENSEA AFTER MINTING!';

function Claim(props) {

   const { maxTokenPurchase, maxTokens, tokenPrice, setStatus, loading, walletAddress, totalSupply, setMintLoading } = props
   const [mintCount, setMintCount] = useState(10);

   async function onMint(event) {
      event.preventDefault()

      if (!walletAddress) {
          setStatus('Please connect with Metamask')
          return
      }
      if (mintCount === 0) {
         setStatus(`Please input the number of Brains.`)
         return          
      }
      if (mintCount > maxTokenPurchase) {
         setStatus(`You can mint maximum up to 20 Brain.`)
         return 
      }

      setMintLoading(true)
      const contract = getContract(walletAddress)
      try {
         let tx = await contract.mintToken(mintCount, { value: BigNumber.from(1e9).mul(BigNumber.from(1e4)).mul(tokenPrice).mul(mintCount), from: walletAddress })
         let res = await tx.wait()
         if (res.transactionHash) {
            setStatus(`You minted ${mintCount} BRAIN Successfully`)
            setMintLoading(false)
         }
      } catch (err) {
         let errorContainer =  (err.error && err.error.message)  ? err.error.message : ''
         let errorBody = errorContainer.substr(errorContainer.indexOf(":")+1)
         let status = "Transaction failed because you have insufficient funds or sales not started"
         errorContainer.indexOf("execution reverted") === -1 ? setStatus(status) : setStatus(errorBody)
         setMintLoading(false)
      }
     
   }

   function onChangeMintCount(isIncrea) {
      let newCount = isIncrea? mintCount + 1 : mintCount - 1
      if ( newCount > 20 ) newCount = 20
      if ( newCount < 0 ) newCount = 0
      setMintCount(newCount)
   }

   return (
      <section id="plain_black" className="container">
            <img id="desktop" alt="" src="images/landing/desktop.png" />
            <img id="mobile" alt="" src="images/landing/mobile.png"/>
         <div className="" id="claim_total">
            <div id="mint_begin" className="text-center">
               {lbl_start_minting}
            </div>
            <div id="claim_brain_text" className="text-center">
               {lbl_claim_title}
            </div>
            <div id="minting">
               <div className=" TotalSupply">
                     <div className="DuckCount">{totalSupply} / {maxTokens}</div>
               </div>
            </div>

            <div id="mint_count">
               <button className="minus-btn" onClick={() => onChangeMintCount(false)}> - </button>
               <input type="number" readOnly value={mintCount} />   
               <button className="plus-btn" onClick={() => onChangeMintCount(true)}> + </button>
            </div>


            <div id="mint_title" className="text-center">
               {maxTokenPurchase} MINT MAX
            </div>
            <div id="mint_content" className="text-center">
               1 BRAIN = {parseFloat(tokenPrice/100000)} ETH
            </div>
            <div className="" id="mint_link_btn">
               {
                  loading ?
                  <a id="mint_btn" href="/" className="text-center" onClick={e => e.preventDefault()}>
                     <div id="mint_now">Minting</div>
                  </a>
                  :
                  <a id="mint_btn" href="/" className="text-center" onClick={e => onMint(e)}>
                     <div id="mint_now">MINT</div>
                  </a>
              }
            </div>
            <div className="text-center">
               <div id="view_brain">{View_brain}</div>
            </div>
         </div>
      </section>
   )
}

export default Claim;
