import React from 'react';

function Faq(props) {
  return  props.faqs ? (
    <section id="faq_black">
      <div id="faq_sector"  className="container">
        <div id="faq" className="text-center">
          <div id="faq_title">FAQ</div>
        </div>
        <img id="lightblue" alt=""   src="images/landing/boundery.png" />
        <div className="row text-center">
          {props.faqs.map((faq, i) =>      
            <div className="col faq_element" key={i}>
              <h3 className="faq_qus_title">{ faq.title }</h3>
              <h4>{ faq.content }</h4>
            </div>
          )}     
        </div>
        <div id="footer_text">Built by NFTMASTERMINDS, All Rights Reserved &copy; {new Date().getFullYear()}</div>
        <div id="faq">
          <img id="footer" alt="" src="images/landing/footer.png" />
        </div>
      </div>
    </section>
  ) : (null)
}

export default Faq;
