import React from 'react';


function Team(Props) {

    return Props.teams ?  (
        <section id="team_back">
            <div className="container">
                <div className="text-center">
                    <h1 id="team_title">TEAM</h1>
                </div>
                <img id="lightblack" alt="" src="images/landing/lightblack.svg" />
                <div className="team_grp text-center">
                    {
                        Props.teams.map((team, i) => 
                            <div className="col-md-3 col-sm-6" key={i}>
                                <div>
                                    <img className="team_img" alt="" src={team.avatar}/>
                                </div>
                                <div className="team_name">{ team.name }</div>
                                <div className="team_position">{ team.position }</div>
                            </div>
                        )
                    }
                </div>
                <div className="text-center">
                    <h1 id="gallery_title">Brainiac Exclusive Previews</h1>
                </div>
            </div>
        </section>
    ) : (null)
        
}

export default Team;
