
import { useEffect, useState } from 'react';
import { BigNumber } from 'ethers'
import { connectWallet, getCurrentWalletConnected, getContract} from './util/interact';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Claim from './Components/Claim';
import Header from './Components/Header';
import Gallery from './Components/Gallery';
import Team from './Components/Team';
import Faq from './Components/Faq';

const faqs = [
  {'title': "What is the total supply?", 'content': "The total supply will be announced soon!"},
  {'title': "When is launch?", 'content': "Launch date will be announced along with supply."},
  {'title': "How much will it cost to mint a Brainiac Society NFT?", 'content': "0.08 ETH + gas."},
  {'title': "Will there be a presale?", 'content': "Yes, but ONLY to active community members who express their passion for this project by sharing, engaging, and truly supporting this movement."},
]
const teams = [
  {'avatar': "images/landing/team002.png", "name": "Mr. Brainiac", "position": "Co Founder"},
  {'avatar': "images/landing/team001.png", "name": "Dr. Brainiac", "position": "Co Founder"},
  {'avatar': "images/landing/team013.png", "name": "Sir Vitaliy", "position": "Development"},
  {'avatar': "images/landing/team014.png", "name": "Sir Genius Brain", "position": "Artwork"},
]
const social_btns = [
  {'link': "https://twitter.com/brainiacnfts", 'text': "BRAINIAC TWITTER", 'text1': "TWITTER"},
  {'link': "https://discord.gg/Vkzv4VRYAk", 'text': "BRAINIAC DISCORD", 'text1': "DISCORD"},
  {'link': "https://www.instagram.com/brainiacnfts", 'text': "BRAINIAC INSTAGRAM", 'text1': "INSTAGRAM"},
]

function App() {

  const [walletAddress, setWalletAddress] = useState();
  const [status, setStatus] = useState(null);
  const [loading, setMintLoading] = useState(false)
  const [tokenPrice, setTokenPrice] = useState(null);
  const [maxTokens, setMaxTokens] = useState(0);
  const [maxTokenPurchase, setMaxTokenPurchase] = useState(0);
  const [totalSupply, setTotalSupply] = useState(0)
  // const [maxTokenPerWallet, setMaxTokenPerWallet] = useState(0);

  const notify = () => toast.info(status, {
    position: "top-right",
    autoClose: 3000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
  });

  useEffect(() => {
    ( async () => {
        let contract = getContract(walletAddress)
        let mtb = await contract.MAX_TOKENS()
        let mtp = await contract.maxTokenPurchase()
        let ts = await contract.totalSupply()
        let tp = await contract.tokenPrice()
        setMaxTokens( parseInt(BigNumber.from(mtb).toString()) )
        setMaxTokenPurchase( parseInt(BigNumber.from(mtp).toString()) )
        setTotalSupply(BigNumber.from(ts).toString())
        setTokenPrice( (BigNumber.from(tp).div(BigNumber.from(1e9).mul(BigNumber.from(1e4))).toString() ) )  // original value * 1e5
    })();
  }, [loading, walletAddress])

  useEffect(() => {
    (async () => { 
      const { address, status } = await getCurrentWalletConnected()
      setWalletAddress(address)
      setStatus(status)
    })();
  }, [])

  useEffect(() => {
    if (status) {
      notify()
      setStatus(null)
    }
  }, [status])

  const onClickConnectWallet = async () => {
    const walletResponse = await connectWallet();
    setStatus(walletResponse.status);
    setWalletAddress(walletResponse.address);
  }

  const onClickDisconnectWallet = async () => {
    setWalletAddress(null)
  }

  
  return (
    <div className="App">
      <Header social_btns={social_btns} onClickDisconnectWallet={onClickDisconnectWallet} onClickConnectWallet={onClickConnectWallet} walletAddress={walletAddress} />
      <Claim maxTokenPurchase={maxTokenPurchase} maxTokens={maxTokens} tokenPrice={tokenPrice} setStatus={setStatus} 
          loading={loading} walletAddress={walletAddress} totalSupply={totalSupply} setMintLoading={setMintLoading}  />
      <Team teams={teams} />
      <Gallery />
      <Faq faqs={faqs} />
      <ToastContainer />
    </div>
  );
}

export default App;
