import React, {useState} from 'react';
import ConnectWallet from './ConnectWallet';

function Header(props) {

   const { social_btns, onClickConnectWallet, onClickDisconnectWallet, walletAddress } = props
   const [modalShow, setModalShow] = useState(false);

   return social_btns ? (
      <header id="home">
         <img id="brainiac_society" alt="" src="images/landing/header_bg.png" />
         <div className="container" id="home">
            <div className="" id="">
               <img id="logo" alt="" src="images/landing/brainiac.png" />
            </div>
            <div className="row connect_btn">
               <div className="col-sm-12">
                  {
                     walletAddress ? 
                        <a rel="noopener noreferrer" onClick={e => onClickDisconnectWallet(e)}><div className="link_btn" >{ walletAddress.slice(0, 11) }...</div></a>
                        :
                        <a rel="noopener noreferrer" onClick={() => setModalShow(true)}><div className="link_btn" >CONNECT</div></a>
                  }
               </div>
            </div>

            <div className="row btn_grp" id="mobile_btn">
               {social_btns.map((social_btn, i) =>     
                     <div className="" key={i}>
                        <a href={social_btn.link} target="_blank" rel="noopener noreferrer"><div className="link_btn">{social_btn.text1}</div></a>
                     </div>
                  )
               }
            </div>
            <div className="row btn_grp" id="desktop_btn">
               {social_btns.map((social_btn, i) =>     
                     <div className="col-sm-4" key={i}>
                        <a href={social_btn.link} target="_blank" rel="noopener noreferrer"><div className="link_btn">{social_btn.text}</div></a>
                     </div>
                  )
               }
            </div>
         </div>
         <ConnectWallet show={modalShow} onHide={() => setModalShow(false)} connectMetamask={onClickConnectWallet} />
      </header>
   ) : (null)
}

export default Header;
